/* Adv mark article */
.token-mark-block {
  display: none;
  position: relative;
  z-index: 20;
  max-width: 1288px;
  width: 100%;
  padding: 0 20px;
  margin: 30px auto 0;
  box-sizing: border-box;
}

.article-mark:hover + .token-info {
  opacity: 1;
  pointer-events: auto;
}

.token-info {
  position: absolute;
  left: 0;
  bottom: 100%;
  width: 270px;
  border: 2px solid #070c39;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(11px);
  border-radius: 2px;
  padding: 20px 10px;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s ease;
}

.token-info-item {
  display: block;
  color: #070c3a;
  font-size: 12px;
  line-height: 1.2;
}
.token-info-item a {
  color: #070c3a;
  text-decoration: underline;
}
.token-info-item a:hover {
  text-decoration: none;
}

/* Оверлей для меню */
.overlay {
  position: fixed;
  inset: 0;
  z-index: 9;
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 0;
  pointer-events: none;
  transition: 0.3s ease;
}

/* Swiper */
.swiper-nav {
  position: absolute;
  z-index: 10;
  display: flex;
  align-items: center;
  gap: 4px;
}
.swiper-nav .swiper-btn-prev,
.swiper-nav .swiper-btn-next {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 70px;
  height: 70px;
  padding: 10px;
  background-color: #1f4cca;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
@media only screen and (max-width: 767px) {
  .swiper-nav .swiper-btn-prev,
  .swiper-nav .swiper-btn-next {
    width: 40px;
    height: 40px;
  }
}
.swiper-nav .swiper-btn-prev:not(.swiper-button-disabled):hover,
.swiper-nav .swiper-btn-next:not(.swiper-button-disabled):hover {
  background-color: rgba(31, 76, 202, 0.7);
}
.swiper-nav .swiper-btn-prev svg,
.swiper-nav .swiper-btn-next svg {
  width: 100%;
  height: 100%;
}
.swiper-nav .swiper-button-disabled {
  filter: grayscale(100%);
  cursor: default;
}

/* Swiper pagination */
.swiper-pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  position: absolute;
  top: unset !important;
  bottom: 5px !important;
  z-index: 12;
}
.swiper-pagination .swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  background-color: #16abff;
  opacity: 1;
  margin: 0 !important;
  transition: 0.3s ease;
}
.swiper-pagination .swiper-pagination-bullet:hover {
  opacity: 0.7;
}
.swiper-pagination .swiper-pagination-bullet-active {
  transform: scale(1.5);
}

/* Custom form from adm */
