  transition: opacity 0.3s ease;
}
.video__play::before, .video__play::after {
  position: absolute;
  inset: 10px;
  content: "";
  display: block;
  border-radius: 50%;
  border: 2px solid rgba(255, 255, 255, 0.2);
}
.video__play::before {
  animation: pulse 2.5s linear infinite;
  animation-delay: 2s;
}
.video__play::after {
  animation: pulse 2.5s linear infinite;
}
.video__play img {
  width: 13px;
  height: 16px;
  margin-left: 4px;
}

.products {
  position: sticky;
  z-index: 10;
  padding: 20px 0;
  transition: 0.3s ease;
